<template>
  <div class="app-container" id="brigadeManagement">
    <div class="filter-container">
      <el-input v-model="listQuery.teamName" placeholder="大队名称" class="filter-item" @keyup.enter.native="handleFilter" />
      <el-button class="filter-item" type="primary" @click="handleFilter" icon="el-icon-search">搜索</el-button>
      <el-button class="filter-item" type="primary" @click="handleCreate" v-if="power==1" icon="el-icon-circle-plus-outline">添加</el-button>
      <el-button class="filter-item filter-item-btn" type="primary" @click="exportExcel" icon="iconfont icondaochu">导出</el-button>

    </div>
    <div class="tableBox">

      <el-table :key="tableKey" v-loading="listLoading" :data="list" fit highlight-current-row style="width: 100%;" :row-class-name="tableRowClassName">
        <el-table-column label="序号" prop="id" fixed sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>
        <el-table-column label="大队名称" min-width="180px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.teamName }}</span>
          </template>
        </el-table-column>
        <el-table-column label="建立日期" min-width="200px" align="center">
          <template slot-scope="scope">
            <span>{{ formatDateTime(scope.row.createTime) }}</span>
          </template>
        </el-table-column>
        <el-table-column label="队长" width="150px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.leaderName }}</span>
          </template>
        </el-table-column>
        <el-table-column label="管辖地区" min-width="200px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.geometryName }}</span>
          </template>
        </el-table-column>
        <el-table-column label="状态" width="150px" align="center">
          <template slot-scope="scope">
            <span>{{ formatStatus(scope.row.status )}}</span>
          </template>
        </el-table-column>

        <el-table-column label="操作" fixed="right" align="center" min-width="360" class-name="small-padding fixed-width" v-if="power==1">
          <template slot-scope="{row}">
            <el-button type="primary" size="mini" @click="handleBind(row)" icon="iconfont iconsuo">绑定</el-button>
            <el-button type="warning" size="mini" @click="handleUnbind(row)" :disabled="row.leaderId==-1&&row.geometryId==-1" icon="iconfont iconjiesuo5">解绑</el-button>
            <el-button type="primary" size="mini" @click="handleUpdate(row)" icon="el-icon-edit-outline">编辑</el-button>
            <el-button type="danger" size="mini" @click="deleteConfirmOpen(row)" icon="el-icon-delete">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />

    <el-dialog id="myDialog" :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible" center>
      <el-form ref="dataForm" :rules="rules" :model="temp" label-position="left" class="formStyle">
        <el-form-item label="大队名称" prop="teamName" v-if="dialogStatus=='create'">
          <el-input v-model="temp.teamName " />
        </el-form-item>
        <el-form-item label="创建时间" prop="createTimeShow">
          <el-date-picker value-format="yyyy-MM-dd HH:mm:ss" v-model="temp.createTimeShow" align="right" type="datetime" placeholder="创建时间" :picker-options="pickerOptions"></el-date-picker>
        </el-form-item>
        <!-- <el-form-item label="队长" prop="leaderId" v-if="dialogStatus=='update'">
          <el-select v-model.number="temp.leaderId" class="filter-item" placeholder="请选择">
            <el-option
              v-for="item in teamLeaderOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="管辖地区" prop="geometryId" v-if="dialogStatus=='update'">
          <el-select
            v-model.number="temp.geometryId"
            class="filter-item"
            placeholder="请选择"
          >
            <el-option
              v-for="item in geometryOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item> -->
        <el-form-item label="状态" prop="status">
          <el-select v-model.number="temp.status" class="filter-item" placeholder="请选择">
            <el-option v-for="item in teamStatusOptions" :key="item.key" :label="item.display_name" :value="item.key" />
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogStatus==='create'?createData():updateData()">确定</el-button>
      </div>
    </el-dialog>
    <el-dialog id="myDialog" :title="bindMap[bindDialogStatus]" :visible.sync="BindDialogFormVisible" center>
      <el-form ref="bindForm" :rules="bindRules" :model="bindTemp" label-position="left" class="formStyle">

        <el-form-item :label="`${bindMap[bindDialogStatus]}类型`" prop="type">
          <el-select filterable v-model="bindTemp.type" class="filter-item" placeholder="请选择">
            <el-option v-for="item in bindTypeOptions" :key="item.id" :label="item.name" :value="item.id" :disabled="item.disabled" />
          </el-select>
        </el-form-item>

        <el-form-item label="队长" prop="leaderId" v-if="bindTemp.type==1&&bindDialogStatus=='bind'">
          <el-select filterable v-model="bindTemp.leaderId" class="filter-item" placeholder="请选择">
            <el-option v-for="item in teamLeaderOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>

        <el-form-item label="路线" prop="routeId" v-if="bindTemp.type==2&&bindDialogStatus=='bind'">
          <el-select filterable v-model="bindTemp.routeId" class="filter-item" placeholder="请选择">
            <el-option v-for="item in geometryOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="bindDialogStatus=='bind'?bindSubmit():unbindSubmit()">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
//大队列表，添加车组，编辑，删除，搜索
import {
  teamlist,
  teamadd,
  teamupdate,
  teamdelete,
  teamSearch,
  bindLeader,
  unbindLeader,
  bindRoute,
  unbindRoute
} from "../../../api/managementArchives/brigadeManagement";
import { formatDate } from "../../../../src/common"; //时间格式化
import {
  personsSelect,
  mapSelect,
  exportExcelReq
} from "../../../api/managementArchives/common"; //人员下拉框数据，区域下拉框数据

let myLoading = undefined; // 弹窗服务调用
//未选择
const unSelect = (rule, value, callback) => {
  if (value == -1) {
    callback(new Error("请选择"));
  } else {
    callback();
  }
};
export default {
  name: "brigadeManagement",
  components: { Pagination },
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      tableKey: 0,
      list: null, //大队列表
      total: 0, //大队数量
      indexMethod: 1, // 序号
      listLoading: true,
      listQuery: {
        page: 1,
        limit: 10,
        teamName: undefined
      },
      teamLeaderOptions: [{ id: -1, name: "请选择" }], //队长下拉数据
      geometryOptions: [{ id: -1, name: "请选择" }], //区域下拉数据
      teamStatusOptions: [
        { key: 0, display_name: "正常" },
        { key: 1, display_name: "取消" }
      ], //大队状态下拉数据
      //添加或编辑使用的数据
      temp: {

        teamName: "",
        createTime: undefined,
        status: 0
      },
      dialogFormVisible: false, //添加或编辑窗口显隐
      dialogStatus: "", //控制是编辑窗口还是添加窗口
      textMap: {
        update: "编辑",
        create: "添加"
      },
      //添加或编辑表单校验
      rules: {
        teamName: [
          { required: true, message: "请填写大队名称", trigger: "blur" }
        ],
        status: [
          { required: true, message: "请选择大队状态", trigger: "change" }
        ]
      },
      downloadLoading: false,
      bindTypeOptions: [
        { id: -1, name: "请选择" },
        { id: 1, name: "队长" },
        { id: 2, name: "路线" }

      ], //绑定类型下拉数据
      bindTemp: {
        teamId: -1,
        type: 1,
        leaderId: -1,
        routeId: -1
      },
      BindDialogFormVisible: false,
      bindobj: {},
      bindDialogStatus: "",//控制是绑定还是解绑窗口
      bindMap: {
        bind: "绑定",
        unbind: "解绑"
      },
      bindRules: {
        type: [
          { required: true, message: "请选择绑定类型", trigger: "change", validator: unSelect }
        ],
        leaderId: [
          { required: true, message: "请选择绑定人员", trigger: "change", validator: unSelect }
        ],
        routeId: [
          { required: true, message: "请选择绑定管辖区域", trigger: "change", validator: unSelect }
        ]
      },
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "昨天",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "一周前",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      }
    };
  },
  created() {
    this.getList(); 
    this.getSelect(); //初始化弹窗的下拉框
  },
  methods: {
     deleteConfirmOpen(row) {
      this.$confirm('确定进行此操作吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.handleDelete(row)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消'
        });
      });
    },
    formatDate1(time) {
      return formatDate(time, 1)
    },
    getList() {
      let that = this;
      this.listLoading = true;
      teamlist({
        page: this.listQuery.page,
        limit: this.listQuery.limit,
        status: [0]
      })
        .then(res => {
          if (res.status == 0) {
            this.getSelect();

            that.listLoading = false;
            that.list = res.data;
            that.total = res.count;
            that.indexMethod =
              (that.listQuery.page - 1) * that.listQuery.limit + 1;
            that.dealData(); //数据处理
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          that.listLoading = false;
        });
    },
    dealData() {
      this.list.forEach((item, index) => {
        this.list[index].createTimeShow = formatDate(item.createTime, 1);

        // if (this.list[index].teamId == -1) {
        //   this.list[index].teamId = null;
        //   this.list[index].teamName = null;
        // }
        // if (this.list[index].groupId == -1) {
        //   this.list[index].groupId = null;
        //   this.list[index].groupName = null;
        // }
      });
    },

    //获取下拉框数据
    getSelect() {
      var that = this;

      mapSelect().then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            that.geometryOptions = [{ id: -1, name: "请选择" }];
            return;
          }
          that.geometryOptions = res.data;
          that.geometryOptions.unshift({ id: -1, name: "请选择" });
        } else {
          if (res.data == null) {
            this.geometryOptions = [];
          }
          this.$message.error(res.msg);
        }
      });
    },
    //点击搜索
    handleFilter() {
      if (
        this.listQuery.teamName == "" ||
        this.listQuery.teamName == undefined
      ) {
        this.listQuery.teamName = null;
        this.getList();
        return;
      }
      teamSearch({
        teamName: this.listQuery.teamName
      }).then(res => {
        if (res.status == 0) {
          let that = this;
          this.listQuery.page = 1;
          this.listLoading = false;
          this.list = res.data;
          this.total = 0;
          this.dealData();
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    //删除
    handleDelete(row) {
      let _t = this;
      _t.listLoading = true;
      teamdelete({
        id: row.id
      })
        .then(res => {
          if (res.status == 0) {
            if (_t.listQuery.page > 1 && _t.list.length <= 1) {
              // 最后一条返回上一页
              _t.listQuery.page -= 1;
            }
            this.getList();
            _t.$message.success("刪除成功");
          } else {
            this.$message.error(res.msg);
            _t.listLoading = false;
          }
        })
        .catch(err => {
          _t.listLoading = false;
          console.error(err);
        });
    },

    //重置
    resetTemp() {
      this.temp = {
        teamName: "",
        status: 0,
        createTimeShow: null,
      };
    },
    selectChange(val) {
      if (val == 1) {
        this.bindTemp.leaderId = this.bindobj.leaderId;
      } else if (val == 2) {
        this.bindTemp.routeId = this.bindobj.geometryId;
      }
    },
    handleBind(row) {
      let that = this;
      personsSelect({ work: 2, teamId: row.id, status: 1 }).then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            that.teamLeaderOptions = [{ id: -1, name: "请选择" }];
            return;
          }
          that.teamLeaderOptions = res.data;
          that.teamLeaderOptions.unshift({ id: -1, name: "请选择" });
        } else {
          if (res.data == null) {
            this.teamLeaderOptions = [];
          }
          this.$message.error(res.msg);
        }
        this.bindTemp = {
          teamId: row.id,
          leaderId: row.leaderId,
          type: 1,
          routeId: row.geometryId
        }
      });
      // this.bindobj={...row}
      this.bindTypeOptions = [
        { id: -1, name: "请选择" },
        { id: 1, name: "队长" },
        { id: 2, name: "路线" }

      ];
      this.bindDialogStatus = "bind";

      this.BindDialogFormVisible = true;
    },
    handleUnbind(row) {
      if (row.leaderId == -1) { this.bindTypeOptions[1].disabled = true }
      if (row.geometryId == -1) { this.bindTypeOptions[2].disabled = true }
      this.bindDialogStatus = "unbind";
      this.bindTemp = {
        teamId: row.id,
        type: -1
      }
      this.BindDialogFormVisible = true;
    },
    bindSubmit() {

      this.$refs["bindForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          let subData;
          if (this.bindTemp.type == 1) {
            subData = {
              teamId: this.bindTemp.teamId,
              leaderId: this.bindTemp.leaderId,
            }
            bindLeader(subData)
              .then(res => {
                if (res.status == 0) {
                  this.getList();
                  this.$message.success(res.msg);
                  this.BindDialogFormVisible = false;
                } else {
                  this.$message.error(res.msg);
                }
              })
              .finally(() => {
                myLoading.close();
              });
          } else if (this.bindTemp.type == 2) {
            subData = {
              routeId: this.bindTemp.routeId,
              teamId: this.bindTemp.teamId,
            }
            bindRoute(subData)
              .then(res => {
                if (res.status == 0) {
                  this.getList();
                  this.$message.success(res.msg);
                  this.BindDialogFormVisible = false;
                } else {
                  this.$message.error(res.msg);
                }
              })
              .finally(() => {
                myLoading.close();
              });
          }


        }
      })
    },
    unbindSubmit() {
      this.$refs["bindForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });

          let subData;
          if (this.bindTemp.type == 1) {
            subData = {
              teamId: this.bindTemp.teamId
            }
            unbindLeader(subData)
              .then(res => {
                if (res.status == 0) {
                  this.getList();
                  this.$message.success(res.msg);
                  this.BindDialogFormVisible = false;
                } else {
                  this.$message.error(res.msg);
                }
              })
              .finally(() => {
                myLoading.close();
              });
          } else if (this.bindTemp.type == 2) {
            subData = {
              teamId: this.bindTemp.teamId,
            }
            unbindRoute(subData)
              .then(res => {
                if (res.status == 0) {
                  this.getList();
                  this.$message.success(res.msg);
                  this.BindDialogFormVisible = false;
                } else {
                  this.$message.error(res.msg);
                }
              })
              .finally(() => {
                myLoading.close();
              });
          }

        }
      })
    },
    //点击增加
    handleCreate() {
      this.resetTemp();
      this.dialogStatus = "create";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    //增加
    createData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          if (this.temp.createTimeShow) { this.temp.createTime = new Date(this.temp.createTimeShow).getTime(); }
          let subData = { ...this.temp };
          delete subData.createTimeShow
          // this.temp.createTime = Date.parse(new Date());
          // this.temp = this.changToNull(this.temp);
          teamadd(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success("添加成功");
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
    //点击编辑
    handleUpdate(row) {
      this.temp = {
        id: row.id,
        status: row.status,
        createTimeShow: row.createTimeShow
      }
      this.dialogStatus = "update";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    //编辑
    updateData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          //由于这几项的下拉选项都是例如[{ id: null, teamName: "请选择" }]这种形式，但是提交的时候需要的是teamId，因此需要转化
          // this.selectFilter(
          //   this.temp.leaderId,
          //   this.teamLeaderOptions,
          //   "id",
          //   "name",
          //   "leaderId",
          //   "leaderName"
          // );
          // this.selectFilter(
          //   this.temp.geometryId,
          //   this.geometryOptions,
          //   "id",
          //   "name",
          //   "geometryId",
          //   "geometryName"
          // );
          // this.temp.createTime = Date.parse(new Date());
          // this.temp = this.changToNull(this.temp);
          if (this.temp.createTimeShow) {

            this.temp.createTime = new Date(this.temp.createTimeShow).getTime();
          }
          let subData = { ...this.temp };
          delete subData.createTimeShow
          teamupdate(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success("编辑成功");
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
    //筛选出select选择的值和内容，参数分别代表（select的v-model值，option数组，option值，option内容，后台需要的id,后台需要的name）
    selectFilter(vId, selectList, id, name, id1, name1) {
      let obj = {};
      obj = selectList.find(item => {
        //这里的selectList就是上面遍历的数据源
        return item[id] === vId; //筛选出匹配数据
      });
      this.temp[name1] = obj[name];
    },

    //格式化时间
    formatDateTime(timestamp) {
      return formatDate(timestamp, 1);
    },
    //大队状态显示
    formatStatus(status) {
      if (status == 0) {
        return "存在";
      } else if (status == 1) {
        return "取消";
      }
    },
    //由于提交的时候如果对应的id为null,对应的name也必须要为null,在这里就统一处理
    changToNull(data) {
      if (data.leaderId == null) {
        data.leaderName = null;
      }
      if (data.geometryId == null) {
        data.geometryName = null;
      }
      return data;
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
    exportExcel() {
      let _t = this;
      // 查找数据
      exportExcelReq({ excelType: 7, title: '大队信息' }).then(res => {
        try {
          let enc = new TextDecoder('utf-8')
          let data = JSON.parse(enc.decode(new Uint8Array(res)))
          this.$message({
            message: data.msg,
            type: 'warning'
          });

        } catch (error) {
          let disposition = res.headers;
          let fileName = "大队信息" + ".xls";
          let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
          let link = document.createElement("a");
          link.href = window.URL.createObjectURL(blob);
          link.download = fileName;
          link.click();
          link.remove();
        }
      });
    }
  }
};
</script>
<style lang="scss">
#brigadeManagement {
}
</style>









