import request from '../../request'
//车组列表
export function teamlist(data) {
    return request({
        url: '/teams/list',
        method: 'post',
        data
    })
}
//添加车组
export function teamadd(data) {
    return request({
        url: '/teams/add',
        method: 'post',
        data
        // headers:{'content-type':'application/json'}
    })
}
//修改车组
export function teamupdate(data) {
    return request({
        url: '/teams/update',
        method: 'post',
        data
        // headers:{'content-type':'application/json'}
    })
}
//删除车组
export function teamdelete(data) {
    return request({
        url: '/teams/remove',
        method: 'post',
        data
    })
}
//根据车组编号搜索车组
export function teamSearch(data) {
    return request({
        url: '/teams/query',
        method: 'post',
        data
    })
}
//绑定队长
export function bindLeader(data) {
    return request({
        url: '/teams/bindLeader',
        method: 'post',
        data
    })
}
//解绑队长
export function unbindLeader(data) {
    return request({
        url: '/teams/unbindLeader',
        method: 'post',
        data
    })
}
//绑定路线
export function bindRoute(data) {
    return request({
        url: '/teams/bindRoute',
        method: 'post',
        data
    })
}
//解绑路线
export function unbindRoute(data) {
    return request({
        url: '/teams/unbindRoute',
        method: 'post',
        data
    })
}