<template>
  <div class="app-container" id="bankPersonManagement">
    <div class="filter-container">
      <el-button class="filter-item filter-item-btn" type="primary" @click="handleCreate" icon="el-icon-circle-plus-outline">添加</el-button>
      <el-button class="filter-item" type="primary" @click="exportExcel" icon="iconfont icondaochu" :loading="exportLoading">导出</el-button>
      <el-button class="filter-item filter-item-btn" type="primary" @click="uploadVisible=true" icon="iconfont icondaoru">导入</el-button>

    </div>
    <div class="tableBox">
      <el-table :key="tableKey" v-loading="listLoading" :data="list" fit highlight-current-row style="width: 100%;" :row-class-name="tableRowClassName">
        <el-table-column label="序号" fixed prop="id" sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>
        <el-table-column label="姓名" width="100px" align="center">
          <template slot-scope="{row}">
            <span class="link-type" @click="handleUpdate(row)">{{ row.name }}</span>
          </template>
        </el-table-column>
        <el-table-column label="身份证号" width="200px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.userCode }}</span>
          </template>
        </el-table-column>
        <el-table-column label="性别" width="80px" align="center">
          <template slot-scope="scope">
            <span>{{ formatSexStatus(scope.row.sex)}}</span>
          </template>
        </el-table-column>
        <el-table-column label="年龄" width="80px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.age==-1?"":scope.row.age }}</span>
          </template>
        </el-table-column>
        <el-table-column label="电话号码" width="120px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.telNum }}</span>
          </template>
        </el-table-column>
        <el-table-column label="地址" min-width="120px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.address }}</span>
          </template>
        </el-table-column>
        <el-table-column label="绑定标签" width="220px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.rfid }}</span>
          </template>
        </el-table-column>
        <el-table-column label="所属银行" width="150px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.parentBankName }}</span>
          </template>
        </el-table-column>
        <el-table-column label="所属营业厅" width="150px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.destinationName }}</span>
          </template>
        </el-table-column>
        <el-table-column label="状态" width="80px" align="center">
          <template slot-scope="scope">
            <span>{{ formatStatus(scope.row.status) }}</span>
          </template>
        </el-table-column>
        <el-table-column label="头像" min-width="80px" align="center" fixed="right">
          <template slot-scope="{row}">
            <span v-if="row.photoId" @click="popShow(row)" style="cursor:pointer">点击显示</span>
            <span v-else style="color: red;">暂无照片</span>

          </template>

        </el-table-column>
        <el-table-column label="操作" align="center" min-width="300" fixed="right" class-name="small-padding fixed-width">
          <template slot-scope="{row}">
            <el-button type="primary" size="mini" @click="handleUpdate(row)" icon="el-icon-edit-outline">编辑</el-button>
            <el-button type="primary" size="mini" @click="handleBind(row)" icon="iconfont iconsuo" v-if="row.rfidId==null">绑定标签</el-button>
            <el-button type="warning" size="mini" @click="unbindSubmit(row)" v-else icon="iconfont iconjiesuo5">解绑标签</el-button>
            <el-button type="primary" size="mini" @click="handleUpload(row)" icon="iconfont iconshangchuan">上传</el-button>

          </template>
        </el-table-column>
      </el-table>
    </div>

    <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />

    <el-dialog id="myDialog" @close="scrollTop" :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible" center>
      <el-form ref="dataForm" :rules="rules" :model="temp" label-position="left" class="formStyle">
        <el-form-item label="姓名" prop="name" v-if="dialogStatus=='create'">
          <el-input v-model="temp.name" />
        </el-form-item>
        <el-form-item label="身份证号" prop="userCode">
          <el-input v-model="temp.userCode" />
        </el-form-item>
        <el-form-item label="性别" prop="sex">
          <el-select v-model.number="temp.sex" class="filter-item" placeholder="Please select">
            <el-option v-for="item in sexOptions" :key="item.key" :label="item.display_name" :value="item.key" />
          </el-select>
        </el-form-item>
        <el-form-item label="年龄" prop="age">
          <el-input v-model.number="temp.age" />
        </el-form-item>
        <el-form-item label="电话号码" prop="telNum">
          <el-input v-model="temp.telNum" />
        </el-form-item>
        <el-form-item label="地址" prop="address">
          <el-input v-model="temp.address" />
        </el-form-item>
        <el-form-item label="状态" prop="status">
          <el-select v-model.number="temp.status" class="filter-item" placeholder="Please select">
            <el-option v-for="item in personStatusOptions" :key="item.key" :label="item.display_name" :value="item.key" />
          </el-select>
        </el-form-item>
        <el-form-item label="所属银行" prop="parentBankId">
          <el-select filterable v-model.number="temp.parentBankId" class="filter-item" placeholder="Please select">
            <el-option v-for="item in bankOptions " :key="item.id" :label="item.name" :value="item.id" :disabled="item.status==2" />
          </el-select>
        </el-form-item>
        <el-form-item label="所属营业厅" prop="destinationId">
          <el-select filterable v-model.number="temp.destinationId" class="filter-item" placeholder="Please select">
            <el-option v-for="item in networkOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogStatus==='create'?createData():updateData()">确定</el-button>
      </div>
    </el-dialog>
    <el-dialog id="myDialog" @close="scrollTop" title="绑定" :visible.sync="BindDialogFormVisible" center>
      <el-form ref="bindForm" :rules="bindRules" label-position="left" class="bindForm formStyle" :model="bindTemp">
        <el-form-item label="标签" prop="rfidId" v-if="bindTemp.type==1&&bindDialogStatus=='bind'">
          <el-select filterable v-model="bindTemp.rfidId " class="filter-item" placeholder="Please select">
            <el-option v-for="item in rfidOptions" :key="item.id" :label="item.rfid" :value="item.id" />
          </el-select>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="bindDialogStatus=='bind'?bindSubmit():unbindSubmit()">确定</el-button>

      </div>
    </el-dialog>
    <el-dialog id="myDialog1" title="上传图片" :visible.sync="DialogUploadVisible" center>
      <el-upload class="upload-demo" ref="my-upload" :multiple="false" :limit="1" drag action="/gate/file/xxx/upload" accept=".jpg,.png" :before-upload="beforeUploadFile" :headers="{Authorization:'Bearer '+ token}" :on-success="uploadSuccess">
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
        <div class="el-upload__tip" slot="tip">只能上传jpg/png文件,且文件大小不得超过10M</div>
        <div class="el-upload__tip red" slot="tip">图片上传尺寸请尽量为 295*413 </div>

      </el-upload>
      <div slot="footer" class="dialog-footer">

      </div>
    </el-dialog>
    <el-dialog id="myDialog2" title="图片" width="30%" :visible.sync="imgVisible" center>
      <img :src="taskSrc" class="taskSrc" alt="暂无图片">
    </el-dialog>
    <my-upload :visible.sync="uploadVisible" :uploadData="{type:5}" @dataUpdated="reqSuccess"></my-upload>
  </div>
</template>

<script>
import myUpload from "@/components/myUpload"; // secondary package based on el-pagination
import Pagination from "../../../components/Pagination"; // secondary package based on el-pagination
import { personDelete, } from "../../../api/managementArchives/personnelFiles";
import { queryR } from "../../../api/inOut/rfid";
import { allDestination } from '../../../api/managementArchives/network'
import { selectBank } from "../../../api/managementArchives/bank";
import { saveOrUpdate, bindRfidBP, unbindRfidBP, queryPage, bankWorkersBindPhoto } from "../../../api/managementArchives/bankPersonManagement";
import { exportExcelReq } from "../../../api/managementArchives/common";
import {
  imgDownload, savePhoto, photoInfosQuery
} from "../../../api/schedule/schedule";
const sexOptions = [
  { key: 1, display_name: "男" },
  { key: 0, display_name: "女" }
];

const personStatusOptions = [
  { key: 0, display_name: "初始化" },
  { key: 1, display_name: "正常" },
  { key: 2, display_name: "删除" }
];
var checkTelNum = (rule, value, callback) => {
  if (!value) {
    callback();
  }
  setTimeout(() => {
    if (!(/^1[3456789]\d{9}$/.test(value))) {
      callback(new Error('手机号码有误，请重填'));
    } else {

      callback();

    }
  }, 1000);
};

let myLoading = undefined; // 弹窗服务调用
export default {
  name: "ComplexTable",
  components: { Pagination, myUpload },
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      token: JSON.parse(sessionStorage.getItem("userInfo"))['Bearer '],

      tableKey: 0,
      list: null,
      total: 0,
      indexMethod: 1, // 序号
      listLoading: false,
      listQuery: {
        page: 1,
        limit: 10,
        name: ""
      },
      sexOptions, //性别下拉项
      personStatusOptions, //状态下拉项
      temp: {
        name: null,
        userCode: "",
        sex: 1,
        age: "",
        officeTime: null,
        status: 0,
        workType: 0
      }, //编辑或者添加需要提交的 数据
      dialogFormVisible: false, //弹窗显隐
      dialogStatus: "", //控制弹窗是添加还是编辑
      textMap: {
        update: "编辑",
        create: "添加"
      },
      //表单验证
      rules: {
        name: [{ required: true, message: "请填写姓名", trigger: "blur" }],
        userCode: [
          { required: true, message: "请填写身份证号", trigger: "blur" }
        ],
        sex: [
          {
            required: true,
            type: "number",
            message: "请选择性别",
            trigger: "change"
          }
        ],
        age: [
          {
            required: true,
            type: "number",
            message: "请填写年龄",
            trigger: "blur"
          }
        ],
        telNum: [
          { validator: checkTelNum, trigger: 'blur' }
        ],
        status: [
          {
            required: true,
            message: "请选择状态",
            trigger: "change"
          }
        ],
        parentBankId: [
          {
            required: true,
            message: "请选择所属银行",
            trigger: "change"
          }
        ],
        destinationId: [
          {
            required: true,
            message: "请选择所属营业厅",
            trigger: "change"
          }
        ],
      },
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "昨天",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "一周前",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
      bindTypeOptions: [
        { id: 1, name: "rfid" }
      ],
      bindTemp: {
      },
      BindDialogFormVisible: false,
      bindDialogStatus: "",//控制是绑定还是解绑窗口
      bindMap: {
        bind: "绑定",
        unbind: "解绑"
      },
      networkOptions: [],
      bankOptions: [],
      rfidOptions: [],
      bindRules: {
        rfidId: [
          { required: true, message: "请选择绑定标签", trigger: "change" }
        ]
      },
      rfidOptions: [],
      scanOptions: [],
      fileList: [],
      DialogUploadVisible: false,
      personId: null,
      imgVisible: false,
      taskSrc: '',
      uploadVisible: false,
      exportLoading: false,
    };
  },
  created() {
    this.getList();
    this.getSelect(); //初始化弹窗的下拉框
  },
  methods: {
    // 上传文件之前的钩子, 参数为上传的文件,若返回 false 或者返回 Promise 且被 reject，则停止上传
    beforeUploadFile(file) {

      let extension = file.name.substring(file.name.lastIndexOf('.') + 1);
      let size = file.size / 1024 / 1024;
      if (extension !== 'jpg' && extension !== 'png' && extension !== 'JPG' && extension !== 'PNG') {
        this.$message.warning('只能上传后缀是.jpg或者.png的文件');
        return false;
      }
      if (size > 10) {
        this.$message.warning('文件大小不得超过10M');
        return false;
      }
    },
    reqSuccess: function (val) {  //val用于接收子组件传过来的值
      if (val == true) {
        this.getList();
      }
    },
    popShow(row) {
      this.listLoading = true;

      this.taskSrc = "";
      let that = this;
      if (row.photoId == null) {
        this.imgVisible = true;
        this.listLoading = false;
        return;
      }
      photoInfosQuery({ id: row.photoId, type: [3] })
        .then(res => {
          if (res.status == 0) {
            if (res.data && res.data.length == 0) {
              that.imgVisible = true;
              return;
            }
            imgDownload({ path: res.data[0].path, fileName: res.data[0].fileName })
              .then(res1 => {
                that.imgVisible = true;
                that.listLoading = false;
                that.taskSrc = window.URL.createObjectURL(res1)
              })
              .finally(() => {
                that.listLoading = false;
              });

          } else {
            this.$message.error(res.msg);
          }
        }).catch(() => {
          that.listLoading = false;
        })
        .finally(() => {
        });

    },
    handleUpload(row) {
      this.DialogUploadVisible = true;
      this.$nextTick(() => {
        this.$refs['my-upload'].clearFiles();
      })

      this.personId = row.id
    },
    uploadSuccess(res) {
      let _t = this;

      if (res.data == null) {
        return;
      }
      let p1 = new Promise((resolve, reject) => {
        savePhoto({
          fileName: res.data.filename,
          path: res.data.path,
          type: 3
        })
          .then(res => {
            resolve(res);

          }).catch(err => {
            reject(err);

          })


      })

      p1.then((res) => {
        bankWorkersBindPhoto({
          personId: this.personId,
          photoId: res.data
        })
          .then(res1 => {
            if (res1.status == 0) {
              this.DialogUploadVisible = false;
              this.$message.success(res1.msg);
              this.getList();

            } else {
              this.$message.error(res1.msg);
            }
          }).catch(() => {
            this.$message.error(res1.msg);
          })
          .finally(() => {
            _t.listLoading = false;
          });


      }, (err) => {
        _t.listLoading = false;
        this.$message.error(err.msg);
      })

      //


    },
    handleBind(row) {
      this.getRfid(row);
      this.bindDialogStatus = "bind";
      this.bindTemp = {
        type: 1,
        personId: row.id,
        rfidId: null,
      }
      this.BindDialogFormVisible = true;
    },
    getRfid(row) {
      let that = this;
      this.rfidOptions = [];
      queryR({ rfidType: [99], status: [0] }).then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            return;
          }
          that.rfidOptions = res.data;

          that.rfidOptions.unshift({ id: null, rfid: "请选择" });
        } else {
          if (res.data == null) {
            this.rfidOptions = [];
            this.rfidOptions.pop();
          }
          this.$message.error(res.msg);
        }
      });
    },

    unbindSubmit(row) {
      let subData, request;
      subData = {
        personId: row.id
      }
      request = unbindRfidBP;
      request(subData)
        .then(res => {
          if (res.status == 0) {
            this.getList();

            this.$message.success(res.msg);
            this.BindDialogFormVisible = false;
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          myLoading.close();
        });
    },
    bindSubmit() {
      this.$refs["bindForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          let subData, request;
          if (this.bindTemp.type == 1) {
            subData = {
              personId: this.bindTemp.personId,
              rfidId: this.bindTemp.rfidId,
            }
            request = bindRfidBP;

          }
          request(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success(res.msg);
                this.BindDialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });

        }
      })
    },

    //获取列表
    getList() {
      let _t = this;
      _t.listLoading = true;
      queryPage({
        page: this.listQuery.page,
        limit: 10,
        status: [0, 1]

      })
        .then(res => {
          if (res.status == 0) {
            this.list = res.data;
            this.total = res.count;
            //处理数据
            this.indexMethod = (_t.listQuery.page - 1) * _t.listQuery.limit + 1;
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          _t.listLoading = false;
        });
    },

    findItem(locations, key, val) {
      let obj = {};
      obj = locations.find((item) => {
        return item[key] === val;
      });
      if (obj == undefined) {
        obj = {};
      }
      return obj;
    },
    //重置
    resetTemp() {
      this.temp = {
        name: null,
        userCode: "",
        sex: 1,
        age: "",
        status: 0,
        workType: 0,
        parentBankId: null,
        parentBankName: null,
        destinationId: null,
        destinationName: null
      };
    },
    //点击添加
    handleCreate() {
      this.resetTemp();
      this.dialogStatus = "create";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },

    //添加
    createData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          let subData = { ...this.temp };
          if (!subData.age && subData.age !== 0) {
            subData.age = -1
          }
          subData.workType = 1
          if (!subData.parentBankId && subData.parentBankId !== 0) {
            delete subData.parentBankId
            delete subData.parentBankName
          } else {
            subData.parentBankName = this.findItem(this.bankOptions, 'id', subData.parentBankId).name
          }
          if (!subData.destinationId && subData.destinationId !== 0) {
            delete subData.destinationId
            delete subData.destinationName

          } else {
            subData.destinationName = this.findItem(this.networkOptions, 'id', subData.destinationId).name
          }
          if (!subData.telNum) {
            subData.telNum = ''
          }
          if (!subData.address) {
            subData.address = ''
          }
          saveOrUpdate(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success("添加成功");
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
    //点击编辑
    handleUpdate(row) {
      this.temp = {
        id: row.id,
        userCode: row.userCode,
        sex: row.sex,
        status: row.status,
        workType: 1,
        parentBankId: row.parentBankId,
        destinationId: row.destinationId,
        address: row.address,
        telNum: row.telNum
      }
      if (row.age !== -1) {
        this.temp.age = row.age
      }

      if (!this.temp.parentBankId && this.temp.parentBankId !== 0) {
        delete this.temp.parentBankId
        delete this.temp.parentBankName

      } else {
        this.temp.parentBankName = this.findItem(this.bankOptions, 'id', this.temp.parentBankId).name
      }
      if (!this.temp.destinationId && this.temp.destinationId !== 0) {
        delete this.temp.destinationId
        delete this.temp.destinationName

      } else {
        this.temp.destinationName = this.findItem(this.networkOptions, 'id', this.temp.destinationId).name

      }
      this.dialogStatus = "update";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    //编辑提交
    updateData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });

          if (!this.temp.parentBankId && this.temp.parentBankId !== 0) {
            delete this.temp.parentBankId
            delete this.temp.parentBankName

          } else {
            this.temp.parentBankName = this.findItem(this.bankOptions, 'id', this.temp.parentBankId).name
          }
          if (!this.temp.destinationId && this.temp.destinationId !== 0) {
            delete this.temp.destinationId
            delete this.temp.destinationName

          } else {
            this.temp.destinationName = this.findItem(this.networkOptions, 'id', this.temp.destinationId).name

          }

          let subData = { ...this.temp };
          if (!subData.age && subData.age !== 0) {
            subData.age = -1;
          }
          subData.officeTime = new Date(this.temp.officeTimeShow).getTime();
          delete subData.officeTimeShow
          if (!subData.telNum) {
            subData.telNum = ''
          }
          if (!subData.address) {
            subData.address = ''
          }
          saveOrUpdate(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success("编辑成功");
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
    //删除
    handleDelete(row) {
      let _t = this;
      _t.listLoading = true;
      personDelete({
        personId: row.id
      })
        .then(res => {
          if (res.status == 0) {
            if (_t.listQuery.page > 1 && _t.list.length <= 1) {
              // 最后一条返回上一页
              _t.listQuery.page -= 1;
            }
            this.getList();
            _t.$message.success("刪除成功");
          } else {
            this.$message.error(res.msg);
            _t.listLoading = false;
          }
        })
        .catch(err => {
          _t.listLoading = false;
        });
    },
    //性别显示
    formatSexStatus(status) {
      if (status == 0) {
        return "女";
      } else if (status == 1) {
        return "男";
      }
    },
    //状态显示
    formatStatus(status) {
      if (status == 0) {
        return "初始化";
      } else if (status == 1) {
        return "正常";
      } else {
        return "删除";
      }
    },
    //职位显示
    formatPostStatus(status) {
      if (status == 1) {
        return "司机";
      } else if (status == 2) {
        return "车组长";
      } else if (status == 3) {
        return "押运员";
      } else if (status == 4) {
        return "业务员";
      } else if (status == 5) {
        return "大队长";
      } else if (status == 6) {
        return "枪管员";
      } else if (status == 7) {
        return "车管员";
      }
      else if (status == 8) {
        return "库管员";
      }
    },

    //初始化下拉框
    getSelect() {
      let that = this;
      selectBank().then(res => {
        this.bankOptions = [];
        if (res.status == 0) {
          if (res.data == null) {

            res.data = []
          }

        } else {
          if (res.data == null) {
            res.data = []
          }
          this.$message.error(res.msg);
        }
        that.bankOptions = res.data;
        // that.bankOptions.unshift({ id: -1, name: "总行" });
        that.bankOptions .unshift({ id: null, name: "请选择" });
      });
      allDestination({ type: 1 }).then(res => {
        if (res.status == 0) {
          if (res.data === null) {
           res.data=[]
          }
         
        } else {
          if (res.data == null) {
          res.data=[]

          }
          this.$message.error(res.msg);
        }
         that.networkOptions=res.data;
          that.networkOptions.unshift({ id: -1, name: "总行" });

          that.networkOptions.unshift({ id: null, name: "请选择" });

      });



    },

    scrollTop() {
      // 弹窗滚动到顶部
      let myDialog = document.querySelector("#myDialog .el-dialog__body");
      myDialog.scrollTop = 0;
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
    exportExcel() {
      let _t = this;
      this.exportLoading = true;
      // 查找数据
      exportExcelReq({ excelType: 28, title: '银行工作人员表' }).then(res => {
        try {
          let enc = new TextDecoder('utf-8')
          let data = JSON.parse(enc.decode(new Uint8Array(res)))
          this.$message({
            message: data.msg,
            type: 'warning'
          });

        } catch (error) {
          let disposition = res.headers;
          let fileName = "银行工作人员表" + ".xls";
          let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
          let link = document.createElement("a");
          link.href = window.URL.createObjectURL(blob);
          link.download = fileName;
          link.click();
          link.remove();
        }
      }).finally(() => {
        this.exportLoading = false;
      });
    }

  }
};
</script>
